.table-container {
  background-color: #212529;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  
}

.table {
  border-collapse: collapse;
  font-size: 1rem;
  width: 100%;
}

.table th,
.table td {
  padding: 10px;
  text-align: left;
  color: white;
  border: 1px solid white;
}

.table th {
  background-color: #212529;
}

.table tbody tr:nth-child(odd) {
  background-color: #212529;
}

.table tbody tr:nth-child(even) {
  background-color: #212529;
}

#dummy-data-heading {
  color: rgb(255, 255, 255); /* Change 'red' to your desired color */
}

